.descriptionBlock{
    grid-area: descrip;
}
.profileInfo{
    border-bottom: 20px #C0B9B0 solid;
    background-color: #fff;
    padding: 30px;
    display: grid;
    grid-template-columns: 3fr 4fr;
    grid-template-areas:"avatar descrip";
}
.avatar{
    grid-area: avatar;
    max-width: 200px;
}
.fullName{
    font-size: 28px;
    font-family: 'Bree Serif', serif;
    margin-top: 0;
    margin-bottom: 10px;
    text-transform:capitalize;
}
.contactsName{
    color: #FCC765;
    font-size: 16px;
}
.countryName{
    margin: 20px 0 5px 0;
    color: #FCC765;
    font-size: 16px;
}
.editButton{
    background-color: #FCC765;
    color: #fff;
    width: 80px;
    cursor: pointer;
    justify-self: end;
    border: none;
    font-family:  'Open Sans', sans-serif;
    font-size: 14px;
    padding: 3px 15px;
    border-radius: 15px;
    margin: 15px 0;
}
.contactsItem img{
    width: 22px;
}
.contactsItem{
    display: grid;
    grid-template-columns: 1fr 13fr;
}
.contactsItem a{
    font-size: 16px;
    padding-left: 3px;
    color: #000;
    vertical-align: top;
    text-decoration: none;
}
.countryItem{
    margin: 0;
    max-width: 300px;
    color: #000;
    font-size: 16px;
}

.fileInput input{
    visibility:hidden;
}
.fileInput{
    margin: 5px auto;
}
.fileInput label{
    border: #FCC765 1px solid;
    border-radius: 15px;
    background-color: #FCC765;
    font-size: 14px;
    color: #fff;
    width: 100px;
    padding: 3px 15px;
    cursor: pointer;
}
input[type="text"] {
    border: none;
}
.contactsEdit{
    display: grid;
    grid-template-columns: repeat(2, auto);
    justify-items: start;
}