.loginErrors {
    color: red;
    font-size: 10px;
    font-style: italic;
}
.login h1{
    font-family:'Open Sans', serif;
    font-size: 28px;
    margin: 0;
    font-weight: normal;
}
.formLogin{
    margin: 10px 0;
}
.formLogin input[type="text"]{
    height: 20px;
    margin-bottom: 10px;
}
.formLogin input[type="password"]{
    height: 20px;
    border:  none;
    margin-bottom: 10px;
}

.formLogin button{
    background-color: #FCC765;
    color: #fff;
    width: 80px;
    cursor: pointer;
    border: none;
    font-family:  'Open Sans', sans-serif;
    font-size: 14px;
    padding: 3px 15px;
    border-radius: 15px;
    margin: 15px 0;
}