.userPhoto img{
    width: 100px;
    border-radius: 50%;
    padding: 20px;
}
.followButton button{
    background-color: #FCC765;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    border-radius: 20px;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    margin-top: 10px;
}
.user{
    width: 400px;
    display: grid;
    grid-template-columns: 150px auto;
    margin: 40px 0;
    background-color: #fff ;
    border-radius: 30px;
}
.userName{
    font-family:'Bree Serif', serif;
    font-size: 24px;
}
.userInfo p{
    margin: 0;
}
.userInfo{
    padding: 20px;
    padding-left: 5px;
}