.item img{
  width: 50px;
  border-radius: 50%;
}
.item{
  border-bottom: 20px #C0B9B0 solid;
  padding: 20px 40px;
  display: grid;
 
}
.profilePost{
  display: grid;
  grid-template-columns: 1fr 10fr 1fr;
  align-items:center;
  grid-gap: 10px;
}
.profilePost p{
  text-transform: capitalize;
  font-family: 'Bree Serif', serif;
  font-size: 18px;
  text-align: left;
}
.deletePost img{
  width: 25px;
}
.deletePost{
  cursor: pointer;
  margin: auto 0;
  padding-right: 10px;
  text-align: right;
  
}
.postMessage{
  padding-left: 40px;
}
.likes{
  cursor: pointer;
  background-color: #FCC765;
  color: #fff;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: repeat(1, 35px);
  justify-self: end;
  align-items: center;
  border-radius: 20px;
  padding: 0 5px;
}
.likesNum{
  display: inline;
  width: 1px;
  padding-left: 5px;
}
.likes img{
  margin: auto 0;
  width: 20px;
  }