.postBlock{
   
    background-color: #fff;
}
.posts{
    margin-top: 10px;
}
textarea{
    resize: none;
    border: #73617C 3px solid;
    height: 70px;
    padding: 5px;
    width: 250px;
    
}
.postButton{
    padding: 3px 15px;
    font-family: 'Open Sans', sans-serif;
    background-color: #FCC765;
    font-size: 14px;
    border: none;
    border-radius: 20px;
    color: #fff;
    cursor: pointer;
    margin-top: 5px;
    
}
.addPost{
    border-bottom: 20px #C0B9B0 solid;
    padding: 25px 30px ;
    text-align: center;
    
}

.postButtonBlock{
    font-size: 14px;
    color: #fff;
    vertical-align:middle;
    
}
.postTextArea{
    text-align: center;
     
}