
  .header{
    padding-left: 50px;
    grid-area: h;
    background-color: #73617C;
    height: 60px;
  }
  .header img{
    margin: auto 0;
    width: 40px;
  }
  .headerContent{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "logo logout";
    width: 60%;
    margin: 0 auto;
  }
  .loginBlock{
    grid-area: logout;
    text-align: right;
    box-sizing:content-box;
    align-self: center;
  }
  
 .loginButton{
  text-decoration: none;
  margin-top: 20px;
  color: #fff;
  font-family: 'Open Sans', sans-serif;
 }
  .logoutButton{
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
    background-color: #73617C;
    border: none;
    color: #FCC765;
    cursor: pointer;

  }
  .loginName{
    
    font-size: 16px;
    margin-top: 13px;
    color: white;
  }
  .loginName p{
    margin: 0;
    padding: 0;
  }
  .name{
    display: inline;
    margin-right: 25px;
  }
.logo{
  grid-area: logo;
  margin-top: 10px;
}